import React, { Component } from 'react';
import Wrapper from "./Wrapper";
import Button from "./Button";

class FixedNavigation extends Component {
    constructor(props) {
        super(props)

        this.state = {
            navigationButtons: [
                { to: "info", classButtonFaIcon: "info-circle" },
                { to: "proyects", classButtonFaIcon: "images" },
                { to: "contact", classButtonFaIcon: "user-friends" },
            ]
        }
    }

    render() {
        return (
            <Wrapper>
                {this.state.navigationButtons.map((button, indx) => 
                <Button 
                    key={indx}
                    to={button.to}
                    classButtonFaIcon={button.classButtonFaIcon} />
                )}
            </Wrapper>
        );
    }
}

export default FixedNavigation;
