import React, { Component } from 'react';
import { Element } from "react-scroll";

class Wrapper extends Component {
    constructor(props) {
        super(props)

        this.state = {
        }
    }

    render() {
        return (
            <Element name={this.props.name} className="element--wrapper">
                <div className="element--wrapper-content">
                    <h1 className="main-title--label">
                        <i className="fas fa-code"></i>
                        &nbsp;{this.props.title}
                    </h1>
                    <>{this.props.children}</>
                </div>
            </Element>
        );
    }
}

export default Wrapper;