import React, { Component } from 'react';
import Wrapper from "../Wrapper";
import SocialNetworkItem from "./SocialNetworkItem";

class Contact extends Component {
    constructor(props) {
        super(props)

        this.state = {
            socialNetworks: [
                { etiqueta:"Facebook", userName:"wilderlizamaG", web:"https://www.facebook.com/wilderlizamaG", icono: "facebook-f", colorvalue:"#3B5998" },
                { etiqueta:"Twitter", userName:"wilderlizama", web:"https://www.twitter.com/wilderlizama", icono: "twitter", colorvalue:"#42C0FB" },
                { etiqueta:"Instagram", userName:"wilder.lizama", web:"https://www.instagram.com/wilder.lizama", icono: "instagram", colorvalue:"#cd486b" },
                { etiqueta:"Github", userName:"wlizama", web:"https://www.github.com/wlizama", icono: "github-alt", colorvalue:"#323131" },
                { etiqueta:"Linkedin", userName:"wilderlizama", web:"https://www.linkedin.com/in/wilderlizama", icono: "linkedin", colorvalue:"#0077B5" },
            ]
        }
    }

    render() {
        return (
            <Wrapper name="contact" title="Contacto">
                <div className="contact--wrapper">
                    <div className="contact-snetworks--wrapper">
                    {this.state.socialNetworks.map((snetwork, indx) =>
                        <SocialNetworkItem key={indx} {...snetwork} />
                    )}
                    </div>
                    <div className="contact-form--wrapper">
                        <form className="contact-form">
                            <fieldset className="contact-form--fieldset">
                                <input type="text" placeholder="Nombres" required/>
                            </fieldset>
                            <fieldset className="contact-form--fieldset">
                                <input type="mail" placeholder="Email" required />
                            </fieldset>
                            <fieldset className="contact-form--fieldset">
                                <textarea rows="4" placeholder="Comentario" required />
                            </fieldset>
                            <div className="contact-form--button-wrapper">
                                <button type="submit">
                                    <span>Enviar</span>
                                    <i class="fas fa-angle-right"></i>
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            </Wrapper>
        );
    }
}

export default Contact;
