import React, { Component } from 'react';
import { Link } from "react-scroll";


class Button extends Component {
    constructor(props) {
        super(props)

        this.state = {
            // states here
        }
    }

    render() {
        return (
            <Link to={this.props.to} smooth={true} spy={true} hashSpy={true} activeClass="fixed-navigation--button-active" className="fixed-navigation--button" >
                <i className={"fas fa-" + this.props.classButtonFaIcon}></i>
            </Link>
        );
    }
}

export default Button;