import React, { Component } from 'react';

class Card extends Component {
    constructor(props) {
        super(props)

        this.state = {
        }
    }

    render() {
        return (
            <article className="proyect-card">
                <h2 className="proyect-card--figure-head" style={{backgroundColor: this.props.data.bgColor}}>{this.props.data.name}</h2>
                <div className="proyect-card--description">
                    <p>{this.props.data.description}</p>
                </div>
                <div>
                    <a href={this.props.data.url} target="_blank" className="proyect-card--link">
                        <span className="proyect-card--link-text">Look at</span>
                        <i className="fas fa-angle-double-right proyect-card--link-ico"></i>
                    </a>
                </div>
            </article>
        );
    }
}

export default Card;
