import React, { Component } from 'react';
import Wrapper from "../Wrapper";

// import "./Info.css"

class Info extends Component {
    constructor(props) {
        super(props)

        this.state = {
            full_name: "Wilder Humberto Lizama Garcia",
            avatar_src: "https://avatars0.githubusercontent.com/u/10176577?s=460&v=4",
            avatar_title: "Yo merenges",
            description: "Programador especializado en Web, apasionado por la tecnología y eternamente autodidacta.",
            location: "Lima - Perú",
            tags: ["programmer", "friki", "full-stack-developer", "free-lance", "curious", "technical-skill", "adaptable"]
        }
    }

    render() {
        return (
            <Wrapper name="info" title="Mi información">
                <div className="info-wrapper">
                    <h2 className="sub-title--label">{this.state.full_name}</h2>
                    <section className="info-section">
                        <figure className="info-figure-wrapper">
                        <img src={this.state.avatar_src} alt={this.avatar_title} title={this.avatar_title} />
                        </figure>
                        <div className="info-texts">
                            <p className="info-description">{this.state.description}</p>
                            <span className="info-location">
                                <i className="fas fa-map-marker-alt"></i>
                                &nbsp;{this.state.location}
                            </span>
                        </div>
                    </section>
                    <div className="info-tags">
                        {this.state.tags.map((tag, indx) =>
                        <span key={indx} className="label-hash">#{tag}</span>
                        )}
                    </div>
                </div>
            </Wrapper>
        );
    }
}

export default Info;
