import React, { Component } from 'react';

class SocialNetworkItem extends Component {
    constructor(props) {
        super(props)

        this.state = {
        }
    }

    render() {
        return (
            <a className="contact-snetworks--item" href={this.props.web}>
                <div className="contact-snetworks--item-ico" style={{backgroundColor: this.props.colorvalue}}>
                    <i className={"fab fa-" + this.props.icono}></i>
                </div>
                <span className="contact-snetworks--item-label">{this.props.etiqueta}</span>
            </a>
        );
    }
}

export default SocialNetworkItem;
