import React, { Component } from 'react';
import Wrapper from "../Wrapper";
import Card from "./Card";

let opacity = 0.6;

class Proyects extends Component {

    constructor(props) {
        super(props)

        this.state = {
            proyects: [
                { name: "github-user-data", description: "Sitio para buscar usuarios en Github", url: "https://wlizama.github.io/github-user-data", bgColor: `rgba(128, 128, 255, ${opacity})` },
                { name: "SUNAT-Billing-Status-SOAP-Client", description: "Cliente para consultar estado y CDR de documentos electrónicos declarados a SUNAT", url: "https://github.com/wlizama/SUNAT-Billing-Status-SOAP-Client", bgColor: `rgba(190, 255, 125, ${opacity})` },
                { name: "web-coast-pacific-group", description: "Proyecto para mejorar la actual página web actiweb.es/coastpacific", url: "http://web-coast-pacific-group.now.sh/", bgColor: `rgba(255, 165, 74, ${opacity})` },
                { name: "web-logistica-intermodal", description: "Sitio web para Logistica Intermodal del Perú", url: "http://logisticaintermodal.net/", bgColor: `rgba(255, 145, 145, ${opacity})` },
                { name: "web-new-transport", description: "Sitio web para New Transport", url: "http://newtransport.net", bgColor: `rgba(125, 255, 255, ${opacity})` }
            ]
        }
    }

    render() {
        return (
            <Wrapper name="proyects" title="Proyectos">
                {this.state.proyects.map((proyecto, indx) =>
                <Card key={indx} data={proyecto} />
                )}
            </Wrapper>
        );
    }
}

export default Proyects;
